const {ActionType} = require("../../constants/Constants");

const initialDialogState = {
    dialog_product: {
        isOpen: false,
        product: {
            images: {
                url_image_main: '',
                url_image_1: '',
                url_image_2: '',
                url_image_3: '',
                url_image_4: '',
            },
            details: {
                product_id: 0,
                name: '',
                price: -1,
                sale_price: -1,
                description_sale: '',
                url_image_main: '',
            }
        }
    },
    cartDialog: {
        isOpen: false
    },
    checkoutDialog: {
        isOpen: false,
        data: {
            order_id : ''
        }
    }
};

const dialogReducer = (state = initialDialogState, action) => {
    switch (action.type) {
        case ActionType.OPEN_PRODUCT_DIALOG:
            return {
                ...state,
                dialog_product: {
                    isOpen: true,
                    product: action.data
                }
            };
        case ActionType.CLOSE_PRODUCT_DIALOG:
            return {
                ...state,
                dialog_product: {
                    ...state.dialog_product,
                    isOpen: false
                }
            };
        case ActionType.OPEN_CART_DIALOG:
            return {
                ...state,
                cartDialog: {
                    ...state.cartDialog,
                    isOpen: true
                }
            }
        case ActionType.CLOSE_CART_DIALOG:
            return {
                ...state,
                cartDialog: {
                    ...state.cartDialog,
                    isOpen: false
                }
            }
        case ActionType.OPEN_CHECKOUT_DIALOG:
            return {
                ...state,
                checkoutDialog: {
                    isOpen: true,
                    data: action.data,
                }
            }
        case ActionType.CLOSE_CHECKOUT_DIALOG:
            return {
                ...state,
                checkoutDialog: {
                    ...state.checkoutDialog,
                    isOpen: false,
                }
            }
        default:
            return state;
    }
};

export default dialogReducer;
