const { ActionType } = require("../../constants/Constants");

const initialOpenMenuState = true;

const hoverTitleReducer = (state = initialOpenMenuState, action) => {
  switch (action.type) {
    case ActionType.ALLOW_TITLE_HOVER:
      return true;
    case ActionType.DISALLOW_TITLE_HOVER:
      return false;
    default:
      return state;
  }
};
export default hoverTitleReducer;
