const {ActionType} = require("../../constants/Constants");

const initialPrefetchDataState = {
    navigation: [],
    locations: {
        list_branch: [{
            address: "Phòng 2328 tòa CT1 - The Pride, đường Tố Hữu, phường La Khê, quận Hà Đông, Hà Nội",
            branch_id: 1,
            description: null,
            email: null,
            name: "Trụ sở văn phòng VIỆT NHẬT",
            phone: "0974491688 - 0966503292",
            time_work: "8h00 - 17h00",
        }],
        total_branch: 0
    },
    hot_product : []
};

const prefetchDataReducer = (state = initialPrefetchDataState, action) => {
    switch (action.type) {
        case ActionType.PREFETCH_DATA_NAV_RECEIVED:
            return {
                ...state,
                navigation: action.data.list,
            };
        case ActionType.PREFETCH_DATA_LOCATION_RECEIVED:
            return {
                ...state,
                locations: {
                    list_branch: action.data.list_branch,
                    total_branch: action.data.total_branch
                }
            }
        case ActionType.PREFETCH_DATA_HOT_PRODUCT_RECEIVED:
            return {
                ...state,
                hot_product: action.data.list
            }
        default:
            return state;
    }
};

export default prefetchDataReducer;
