import React from "react";
import "./Loading.css";

export default function Loading(props) {
  if (props.error) {
    return (
      <div style={{ width: "100%" }}>
        <div className="loading-error-container">
          <div className="loading-sad-icon-container">
            <i className="fas fa-frown"></i>
          </div>
          <div className="loading-error-wrapper">
            <span className="loading-error-text">
              Đã có 1 số lỗi bất thường xảy ra, hãy thử truy cập lại trang web
            </span>
            <button className="loading-error-button" onClick={props.retry}>
              Thử lại
            </button>
          </div>
        </div>
      </div>
    );
  } else if (props.timedOut) {
    return (
      <div style={{ width: "100%" }}>
        <div className="loading-error-container">
          <div className="loading-sad-icon-container">
            <i className="fas fa-stopwatch"></i>
          </div>
          <div className="loading-error-wrapper">
            <span className="loading-error-text">
              Truy cập quá lâu, hãy thử lại
            </span>
            <button className="loading-error-button" onClick={props.retry}>
              Thử lại
            </button>
          </div>
        </div>
      </div>
    );
    // props.pastDelay
  } else if (true) {
    return (
      <div className="loading-delay-container">
        <div className="loading-delay dot1"></div>
        <div className="loading-delay dot2"></div>
        <div className="loading-delay dot3"></div>
      </div>
    );
  } else {
    return null;
  }
}
