const { ActionType } = require("../../constants/Constants");

const initialOpenMenuState = false;

const openMenuReducer = (state = initialOpenMenuState, action) => {
  switch (action.type) {
    case ActionType.OPEN_HOME_MENU:
      return true;
    case ActionType.CLOSE_HOME_MENU:
      return false;
    default:
      return state;
  }
};
export default openMenuReducer;
