import {getItems} from "../../api/api";
import {put, takeLatest, all, call} from "redux-saga/effects";
import {ActionType} from "../../constants/Constants";
import {useHistory} from 'react-router-dom'

//search product reducer
function* fetchProductBySearch(action) {
    try {
        const data = yield call(getItems, action.url, {timeout: 20000});
        yield put({type: ActionType.SEARCH_DATA_RECEIVED, products: data.data.list});
    } catch (e) {
        yield put({type: ActionType.SEARCH_DATA_REQUEST_FAILED});
    }
}

function* watchFetchProductBySearch() {
    yield takeLatest(ActionType.SEARCH_DATA_REQUESTED, fetchProductBySearch);
}

//prefetch-category
function* prefetchNavData(action) {
    try {
        const data = yield call(getItems, '/category', {timeout: 20000});
        yield put({
            type: ActionType.PREFETCH_DATA_NAV_RECEIVED, data: data.data
        })
    } catch (e) {
    }
}

function* watchPrefetchNavData() {
    yield takeLatest(ActionType.PREFETCH_DATA_NAV_REQUESTED, prefetchNavData)
}

// prefetch-location
function* prefetchLocationData(action) {
    try {
        const data = yield call(getItems, '/branch', {timeout: 20000});
        yield put({
            type: ActionType.PREFETCH_DATA_LOCATION_RECEIVED, data: data.data
        })
    } catch (e) {
    }
}

function* watchPrefetchLocationData() {
    yield takeLatest(ActionType.PREFETCH_DATA_LOCATION_REQUESTED, prefetchLocationData)
}

//hot product
function* prefetchHotProductData(action) {
    try {
        const data = yield call(getItems, `/all_product?page=1&item_per_page=8&order=is_hot,desc`, {timeout: 20000});
        yield put({
            type: ActionType.PREFETCH_DATA_HOT_PRODUCT_RECEIVED, data: data.data
        })
    } catch (e) {
    }
}
function* watchFetchHotProduct() {
    yield takeLatest(ActionType.PREFETCH_DATA_HOT_PRODUCT_REQUESTED, prefetchHotProductData)
}

//root saga
export default function* rootSaga() {
    yield all([watchFetchProductBySearch(),
        watchPrefetchNavData(), watchPrefetchLocationData(), watchFetchHotProduct()]);
}
