import axios from "axios";
const BACK_END = 'https://maythucphamvietnhat.com/api/public'
// const BACK_END = 'http://localhost:5000/public'
export const getItems = (url, config = {}) => {
    return axios.get(BACK_END + url, config);
};

export const postItems = (url, data = {}, config = {}) => {
    return axios.post(BACK_END + url, data, config)
}
