import { ActionType } from "../../constants/Constants";

const initialSearchProductState = {
  isLoading: false,
  products: [],
};
const searchProductReducer = (state = initialSearchProductState, action) => {
  switch (action.type) {
    case ActionType.SEARCH_DATA_REQUESTED:
      return { ...state, isLoading: true };
    case ActionType.SEARCH_DATA_RECEIVED:
      return { ...state, isLoading: false, products: [...action.products] };
    case ActionType.SEARCH_DATA_REQUEST_FAILED:
      return initialSearchProductState;
    case ActionType.SEARCH_DATA_CLEARED:
      return initialSearchProductState;
    default:
      return state;
  }
};

export default searchProductReducer;
