import searchProductReducer from "../reducers/searchProductReducer";
import { createStore, combineReducers, applyMiddleware } from "redux";
import createSagaMiddleware from "redux-saga";
import rootSaga from "../saga/index";
import openMenuReducer from "../reducers/openMenuReducer";
import prefetchDataReducer from "../reducers/prefetchDataReducer";
import cartReducer from "../reducers/cartReducer"
import hoverTitleReducer from "../reducers/hoverTitleReducer";
import dialogReducer from "../reducers/dialogReducer";
const saver = (store) => (next) => (action) => {
  next(action);
  localStorage.setItem("redux-cart", JSON.stringify(store.getState().cartReducer));
};

const sagaMiddleWare = createSagaMiddleware();

const reducer = combineReducers({
  searchProductReducer,
  openMenuReducer,
  prefetchDataReducer,
  cartReducer,
  hoverTitleReducer,
  dialogReducer
});
const store = createStore(reducer, localStorage.getItem('redux-cart')? {
  cartReducer: JSON.parse(localStorage.getItem('redux-cart'))
}:{cartReducer: []}, applyMiddleware(sagaMiddleWare, saver));

sagaMiddleWare.run(rootSaga);

export default store;
