const { ActionType } = require("../../constants/Constants");

const initialCartState = []
const cartReducer = (state = initialCartState, action) => {
  switch (action.type) {
    case ActionType.ADD_TO_CARD:{
      let is_Exist = false;
      let newState = state.map((item, index)=>{
        let amount = item.amount
        if (item.product_id === action.data.product_id) {
          is_Exist = true;
          amount = amount + action.data.amount
          return {
            product_id: item.product_id,
            amount: amount,
            name: action.data.name,
            price: action.data.price,
            sale_price: action.data.sale_price,
            url_image_main: action.data.url_image_main
          }
        }
        else {
          return item
        }
      })
      if (!is_Exist) {
        newState.push({
          product_id: action.data.product_id,
          amount: action.data.amount,
          price : action.data.price,
          sale_price : action.data.sale_price,
          url_image_main : action.data.url_image_main,
          name : action.data.name
        })
      }
      return newState
    }
    case ActionType.REMOVE_FROM_CARD:
    {
      let newState = state.filter((item)=>{
          return item.product_id !== action.data.product_id
      })
      return newState
    }
    case ActionType.CHANGE_VALUE_CART:
    {
      let newState = state.map((item)=>{
        if (item.product_id === action.data.product_id)
        {
          return {
            ...item,
            amount : action.data.amount
          }
        }
        else{
          return item
        }
      })
      return newState
    }
    case ActionType.REMOVE_ALL_CART: {
      return initialCartState
    }
    default:
      return state;
  }
};

export default cartReducer;
